
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
}

h1 {
  border-bottom: 1px solid #eee;
}
p {
  color: #348FEE;
}
.flex-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.flex-row .flex-1 {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.markdown-wrapper {
  padding: 0 20px;
}
.markdown-text {
  width: 100%;
  height: 600px;
}

.vue-markdown-run {
  margin-top: 15px;
  margin-bottom: 15px;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  -webkit-transition: .2s;
  transition: .2s;
  padding: 15px;
}
.vue-markdown-run:hover {
    -webkit-box-shadow: 0 0 10px 1px #ebebeb;
            box-shadow: 0 0 10px 1px #ebebeb;
}

.btn {
  overflow: hidden;
  position: relative;
  font-size: 14px;
  border-width: 1px;
  border-style: solid;
  outline: none;
  cursor: pointer;
  padding: 8px 20px;
  border-radius: 4px;
  -webkit-transition: .3s;
  transition: .3s;
  display: inline-block;
}
.btn.btn-round {
    border-radius: 20px;
    padding: 12px 23px;
}
.btn.btn-md {
    padding: 10px 23px;
    font-size: 15px;
}
.btn-default {
  border-color: #dbdbdb;
  background-color: white;
  color: #4A4A4A;
}
.btn-default:hover {
    border-color: #d1d1d1;
    background-color: #f0f0f0;
}
.btn-default.disabled:hover {
    border-color: #dbdbdb;
    background-color: white;
}
.btn-red {
  border-color: #f83d3d;
  background-color: #f95a5a;
  color: #ffffff;
}
.btn-red:hover {
    border-color: #F72929;
    background-color: #F72929;
}
.btn-red.disabled:hover {
    border-color: #f83d3d;
    background-color: #f95a5a;
}
.btn-blue {
  border-color: #4799f0;
  background-color: #63a9f2;
  color: #ffffff;
}
.btn-blue:hover {
    border-color: #348FEE;
    background-color: #348FEE;
}
.btn-blue.disabled:hover {
    border-color: #4799f0;
    background-color: #63a9f2;
}
.btn-green {
  border-color: #00ae6e;
  background-color: #00cd81;
  color: #ffffff;
}
.btn-green:hover {
    border-color: #009a61;
    background-color: #009a61;
}
.btn-green.disabled:hover {
    border-color: #00ae6e;
    background-color: #00cd81;
}
.disabled {
  cursor: not-allowed;
}
.zk-btn {
  overflow: hidden;
  position: relative;
}
.zk-ripple {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
